@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #151527;
  color: #ffff;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #ffff;
}

.search-input {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.app-text {
  margin-bottom: 32px;
  text-align: center;
}

.search-input {
  padding-left: 26px;
  padding-right: 26px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(to right top, #30074f, #33125e, #351d6d, #34277c, #31318c);
  color: #ffff;
  outline: none;
}

.search-input:valid {
  box-shadow:none;
}

.search-input::-webkit-input-placeholder {
  color: #ffff;
  font-weight: 600;
}

.search-input:-ms-input-placeholder {
  color: #ffff;
  font-weight: 600;
}

.search-input::placeholder {
  color: #ffff;
  font-weight: 600;
}



.coin-conteiner {
    display: flex;
    justify-content: center;
}

.coin-row {
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    width: 950px;
    border-bottom: 1px solid #ffff;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin h1 {
    font-size: 16px;
    width: 150px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    text-transform: uppercase;
}

.coin-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price {
    width: 110px;
}

.coin-volume {
    width: 200px;
}

.coin-marketcap {
    width: 280px;
}

.coin-percent {
    width: 70px;
}

.red {
    color: #f00606;
}

.green {
    color: #11d811;
}



